import Vue from "vue";
import Vuex from "vuex"

Vue.use(Vuex);

//surprisingly painless guide for using Vuex: 
//this is really nice because it allows less bloat in the data part of App.vue

export default new Vuex.Store({
    state:{
        visibilityController:{
//a setting for each component that will have toggleable visibility
        },
        userKnownLists:{
			knownKanji: ["勉", "強"],
			knownWords: ["勉強"],
		},
        userSettings:{} //things like display romaji and stuff
    },
    getters:{},
    mutations:{ //this is how you update the values in state
        addKanji(state, pl){ //you're apparently meant to be able to pass in objects as the payload often but i cant figure out how
            // console.log(payload.kanji);
            // console.log(payload);
            let toAdd = pl.payload;
            if(!state.userKnownLists.knownKanji.includes(toAdd)){
                state.userKnownLists.knownKanji.push(toAdd);
                
            }
            console.log(state.userKnownLists.knownKanji);
        },
        addWord(state, pl){
            let toAdd = pl.payload;
            if(!state.userKnownLists.knownWords.includes(toAdd)){
                state.userKnownLists.knownWords.push(toAdd);
            }
        },
        //need to update these to actually work, i dont know if i need them yet
        addKanjiArray(state, payload){
            console.log(payload);
            console.error("not implemented yet");
            // state.userKnownLists.knownKanji.push(payload.kanji);
        },
        addWordArray(state, payload){
            console.log(payload);
            console.error("not implemented yet");
            // state.userKnownLists.knownWords.push(payload.word);
        }
    },
    actions:{}
})