			kanjiToAdd: "",

			wordToAdd: "",
		};

		let jishoSearchData = {
			searchOutputArray: [],
			searchTerm: "いたい",
		};

		let userLists = {
			knownKanji: ["勉", "強"],
			knownWords: ["勉強"],
		};

		let navigationInfo = {
			currentTab: "Search Words By Character",
			tabs: ["Search Words By Kanji", "Known Words and Kanji"],
		};

		let dynamicCSSClasses = ["kanjiChar", "kanaChar"];

		let kanjiApiURL = "https://kanjiapi.dev/v1/";
		let jishoApiProxyURL =
			"https://people.rit.edu/msa4295/330/japanese-progress-tracker/jisho-api-proxy.php?";

		return {
			// searchOutputArray: [], //oh god this name needs to be changed to results array or something. maybe kanjiWords
