	methods: {
		searchKanjiWords() {
			//search the Kanji api for words with the given Kanji
			this.kanjiSearchData.searchOutputArray = []; //todo: name change
			let wordAddOnURL = "words/";
			console.log("searchingggg " + this.kanjiSearchData.kanjiToSearch);
			let response = util.fetchApiData(
				this.kanjiApiURL + wordAddOnURL,
				this.kanjiSearchData.kanjiToSearch
			);
			for (const obj of response) {
				this.kanjiSearchData.searchOutputArray.push(obj);
			}
		},
		searchKanjiCharacter() {},
		searchJishoWord() {},
		addCharacter(charFromHTML = undefined) {
			if (charFromHTML) {
				if (util.isKanji(charFromHTML)) {
					console.log("adding " + charFromHTML);
					this.userLists.knownKanji.push(charFromHTML);
					charFromHTML = "";
				}
			}
			// console.log("adding " + this.kanjiSearchData.kanjiToAdd);
			//     this.userLists.knownKanji.push(this.kanjiSearchData.kanjiToAdd);
			//     this.kanjiSearchData.kanjiToAdd = "";
		},
		addVocab(wordFromHTML = undefined) {
			if (wordFromHTML) {
				console.log("adding " + wordFromHTML);
				this.userLists.knownWords.push(wordFromHTML);
				this.kanjiSearchData.wordToAdd = "";
			} else {
				console.log("adding " + this.kanjiSearchData.wordToAdd);
				this.userLists.knownWords.push(this.kanjiSearchData.wordToAdd);
				this.kanjiSearchData.wordToAdd = "";
			}
		},
		findKnownWords() {
			this.kanjiSearchData.searchOutputArray = []; //todo: name change
			let wordsArray = [];

			//api call
			for (const char of this.userLists.knownKanji) {
				fetch("https://kanjiapi.dev/v1/words/" + char)
					.then((response) => {
						if (!response.ok) {
							throw Error(`ERROR: ${response.statusText}`);
						}
						return response.json();
					})
					.then((json) => {
						console.log(json);
						for (const obj of json) {
							wordsArray.push(obj);
							this.kanjiSearchData.searchOutputArray.push(obj);
						}
					});
			}

			//loop through every word object
			for (const word of wordsArray) {
				//extract the kanji writing of the word
				let writing = word.variants[0].written;
				//extract just the kanji from the word (remove any kana)
				let kanjiInWord = util.getKanji(writing);
				//check if each kanji is in the "Known" list
				//https://stackoverflow.com/questions/38811421/how-to-check-if-an-array-is-a-subset-of-another-array-in-javascript
				let shouldKnowKanji = kanjiInWord.every((k) =>
					this.userLists.knownKanji.includes(k)
				);
				//if not, then don't show this word (remove from kanji array)
				if (shouldKnowKanji) {
					this.kanjiSearchData.searchOutputArray.push(word);
				}
			}
		},
		// styleKanjiKana(char) {
		// 	console.log(util.isKanji(char));
		// 	if (util.isKanji(char)) {
		// 		return "kanjiChar";
		// 	} else {
		// 		return "kanaChar";
		// 	}
		// },
		//v-bind:class="styleKanjiKana(char)"
	},
