		},
		findKnownWords() {
			//DONT USE REFERENCE ONLY
			this.kanjiSearchData.searchOutputArray = []; //todo: name change
			let wordsArray = [];

			//api call
			//why is my bud doing an api call here why not the other method
			for (const char of this.$store.state.userKnownLists.knownWords) {
				fetch("https://kanjiapi.dev/v1/words/" + char)
					.then((response) => {
						if (!response.ok) {
							throw Error(`ERROR: ${response.statusText}`);
						}
						return response.json();
					})
					.then((json) => {
						console.log(json);
						for (const obj of json) {
							wordsArray.push(obj);
							this.kanjiSearchData.searchOutputArray.push(obj);
						}
					});
			}

			//loop through every word object
			for (const word of wordsArray) {
				//extract the kanji writing of the word
				let writing = word.variants[0].written;
				//extract just the kanji from the word (remove any kana)
				let kanjiInWord = util.getKanji(writing);
				//check if each kanji is in the "Known" list
				//https://stackoverflow.com/questions/38811421/how-to-check-if-an-array-is-a-subset-of-another-array-in-javascript
				let shouldKnowKanji = kanjiInWord.every((k) =>
					this.userLists.knownKanji.includes(k)
				);
				//if not, then don't show this word (remove from kanji array)
				if (shouldKnowKanji) {
					this.kanjiSearchData.searchOutputArray.push(word);
				}
			}
		},
