<template>
	<div>
		<b-form-input
			size="lg"
			v-model.trim="kanjiToSearch"
			placeholder="Enter a kanji or group of kanji"
		/>
		<button class="btn btn-outline-success" @click="searchKanji">
			Search
		</button>
		<div v-if="showLoading">Loading results...</div>
		<!-- I want to give more padding to these -->
		<b-container v-if="kanjiSearchOutput[0]">
			<p>{{ kanjiSearchOutput.length }} Kanji searched</p>
			<b-row v-for="element in kanjiSearchOutput" v-bind:key="element.id">
				<b-col>
					<h1>{{ element.kanji }}</h1>
					<h4>Meanings:</h4>
					<ol>
						<li v-for="m in element.meanings" v-bind:key="m.id">
							{{ m }}
						</li>
					</ol>
				</b-col>
				<b-col>
					<div>
						<h4>Kun-readings:</h4>
						<ol>
							<li
								v-for="m in element.kun_readings"
								v-bind:key="m.id"
							>
								{{ m }}
							</li>
						</ol>
						<h4>On-readings:</h4>
						<ol>
							<li
								v-for="m in element.on_readings"
								v-bind:key="m.id"
							>
								{{ m }}
							</li>
						</ol>
					</div>
				</b-col>
				<b-col>
					<div>
						<p>Taught in grade: {{ element.grade }}</p>
						<p>JLPT Level: {{ element.jlpt }}</p>
						<p>Strokes: {{ element.stroke_count }}</p>
					</div>
				</b-col>
				<IKnowThisKanjiBtn :kanji="element.kanji"></IKnowThisKanjiBtn>
			</b-row>
		</b-container>
		<!-- <table class="table table-striped table-sm"></table> -->
	</div>
</template>

<script>
import * as util from "../util.js";
import IKnowThisKanjiBtn from "./IKnowThisKanjiBtn.vue";

import Vue from "vue";
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";

import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

export default {
	name: "KanjiCharSearchResults",
	components: {
		IKnowThisKanjiBtn,
	},
	props: ["kanjiApiURL", "passedInKanji"],
	data: function () {
		// let data_kanjiSearchData = this.props[0];
		// let kaji = this.searchKanjiWords();
		// let kanjiToSearch = "";
		return {
			showLoading: false,
			kanjiToSearch: "",
			kanjiSearchOutput: [],
		};
	},
	methods: {
		searchKanji() {
			//search the Kanji api for the specific given Kanji characters

			//dont search if there is nothing to search
			if (this.kanjiToSearch == "" || this.kanjiToSearch == null) return;

			this.showLoading = true;
			this.kanjiSearchOutput = [];
			let wordAddOnURL = "kanji/";
			console.log(
				"searchingggg with single character Component" +
					this.kanjiToSearch
			);
			for (const char of this.kanjiToSearch) {
				fetch(this.kanjiApiURL + wordAddOnURL + char)
					.then((response) => {
						if (!response.ok) {
							throw Error(`ERROR: ${response.statusText}`);
						}
						return response.json();
					})
					.then((json) => {
						// this.kanjiSearchOutput = json;
						console.log(json);
						// responseArray.push(obj);
						this.kanjiSearchOutput.push(json);
						this.showLoading = false;
					});
			}
			if (this.kanjiSearchOutput == []) {
				console.log("make a no results thing bruh");
			}
		},
	},
	created() {
		if(this.passedInKanji != undefined)
			this.kanjiToSearch = this.passedInKanji;
		this.searchKanji();
	},
};
</script>

