<template>
	<div>
		<b-form-input
			size="lg"
			v-model.trim="wordToSearch"
			placeholder="Enter a word in English, Japanese, or Romaji"
		/>
		<button class="btn btn-outline-success" @click="searchJishoWord">
			Search
		</button>
		<div v-if="showLoading">Loading results...</div>
		<div v-if="jishoSearchOutput[0]">

		<p>{{ jishoSearchOutput.length }} results</p>
		<table class="table table-striped table-sm">
			<tr>
				<th>Word</th>
				<th>English</th>
			</tr>
			<tr v-for="entry in jishoSearchOutput" v-bind:key="entry.id">
				<td>
					<!-- need to loop through each character for kanji -->
					<!-- {{ entry.japanese[0].word }} -->
					<StyledKanjiWord
						:word="entry.japanese[0].word"
					></StyledKanjiWord>
					<p>{{ entry.japanese[0].reading }}</p>

					<IKnowThisWordBtn
						:word="entry.japanese[0].word"
					></IKnowThisWordBtn>
				</td>
				<td>
					{{ entry.senses[0].english_definitions[0] }}
					<!-- <br />
					{{ entry.japanese[0].reading }} -->
				</td>
			</tr>
		</table>
		</div>
	</div>
</template>

<script>
import * as util from "../util.js";
import IKnowThisWordBtn from "./IKnowThisWordBtn.vue";
import StyledKanjiWord from "./StyledKanjiWord.vue";

import Vue from "vue";
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";

import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

export default {
	name: "JishoWordSearch",
	components: {
		StyledKanjiWord,
		IKnowThisWordBtn,
	},
	props: ["jishoApiURL", "passedInWord"],
	data: function () {
		// let data_kanjiSearchData = this.props[0];
		// let kaji = this.searchKanjiWords();
		return {
			showLoading: false,
			wordToSearch: "",
			jishoSearchOutput: [],
		};
	},
	methods: {
		searchJishoWord() {
			//dont search if there is nothing to search
			if (this.wordToSearch == "" || this.wordToSearch == null) return;

			this.jishoSearchOutput = []; //todo: name change

			this.showLoading = true;
			// console.log("searching " + this.wordToSearch);
			let apiAttributeName = "keyword=";
			fetch(this.jishoApiURL + apiAttributeName + this.wordToSearch)
				.then((response) => {
					if (!response.ok) {
						throw Error(`ERROR: ${response.statusText}`);
					}
					// console.log(response);
					return response.json();
				})
				.then((json) => {
					console.log(json);
					// this.jishoSearchData.searchOutputArray = [];
					// console.log(JSON.stringify(json));
					for (const obj of json.data) {
						//need the data array not the meta
						this.jishoSearchOutput.push(obj);
					}

					this.showLoading = false;
				});
			console.log(this.jishoSearchOutput);
		},
	},
	created() {
		if (this.passedInWord != undefined)
			this.kanjiToSearch = this.passedInWord;
		this.searchJishoWord();
	},
};
</script>

<!-- need to make external -->
<style>
.kanjiPar {
	display: inline-block;
}
.furigana {
	font-size: xx-small;
}
.kanaChar {
	font-size: medium;
}

.kanjiChar {
	font-size: xx-large;
}
</style>
