<template>
	<div>
		<b-form-input
			size="lg"
			v-model.trim="kanjiToSearch"
			placeholder="Enter a Kanji to find words written with it"
		/>

		<button class="btn btn-outline-success" @click="searchKanjiWords">
			Search
		</button>
		<div v-if="showLoading">Loading results...</div>
		<div v-if="kanjiToSearch[1]">
			Please only search one character at a time
		</div>
		<div v-if="wordSearchOutput[0]">
			<p>
				{{ wordSearchOutput.length }} words containing the character
				{{ kanjiToSearch }}
			</p>
			<p>Click characters to add them to your Known list</p>
			<!-- make whole table the component -->
			<!-- <table-kanji-search-results
							:kanjiWordSearchData="kanjiWordSearchData"
						></table-kanji-search-results> -->
			<div>
				<table class="table table-striped table-sm">
					<tr>
						<th>Word</th>
						<!-- <th>Word</th> -->
						<th>English</th>
					</tr>
					<tr v-for="word in wordSearchOutput" v-bind:key="word.id">
						<!-- uuuuh these should probably be using w not k -->
						<td>
							<StyledKanjiWord
								:word="word.variants[0].written"
							></StyledKanjiWord>
							<p>
								<span> {{ word.variants[0].pronounced }}</span>
							</p>
							<IKnowThisWordBtn
								:word="word.variants[0].written"
							></IKnowThisWordBtn>
							<!-- does not work, idk why -->
							<!-- <button class="btn btn-outline-success" @click="addVocab">Add to Known Words</button> -->
						</td>
						<!-- <table-word v-bind:wordData="k"></table-word> -->
						<td>
							<ol>
								<li
									v-for="m in word.meanings[0].glosses"
									v-bind:key="m.id"
								>
									{{ m }}
								</li>
							</ol>
						</td>
					</tr>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
import * as util from "../util.js";
import IKnowThisWordBtn from "./IKnowThisWordBtn.vue";
import StyledKanjiWord from "./StyledKanjiWord.vue";

import Vue from "vue";
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";

import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

export default {
	name: "KanjiWordSearchResults",
	components: {
		IKnowThisWordBtn,
		StyledKanjiWord,
	},
	props: ["kanjiApiURL"],
	data: function () {
		// let data_kanjiWordSearchData = this.props[0];
		// let kaji = this.searchKanjiWords();
		return {
			showLoading: false,
			kanjiToSearch: "",
			wordSearchOutput: [],
		};
	},
	methods: {
		searchKanjiWords() {
			//search the Kanji api for words with the given Kanji

			//dont search if there is nothing to search
			if (this.kanjiToSearch == "" || this.kanjiToSearch == null) return;
			if (this.kanjiToSearch.length > 1) return;

			this.showLoading = true;
			this.wordSearchOutput = [];
			let wordAddOnURL = "words/";
			console.log("searchingggg with Component" + this.kanjiToSearch);

			fetch(this.kanjiApiURL + wordAddOnURL + this.kanjiToSearch)
				.then((response) => {
					if (!response.ok) {
						throw Error(`ERROR: ${response.statusText}`);
					}
					return response.json();
				})
				.then((json) => {
					// console.log(json);
					for (const obj of json) {
						// responseArray.push(obj);
						this.wordSearchOutput.push(obj);
					}
					this.showLoading = false;
				});
			if (this.wordSearchOutput == []) {
				console.log("make a no results thing bruh");
			}
		},
		addCharacter(char) {
			// console.log(char);
			if (util.isKanji(char)) {
				console.log("adding " + char);
				this.$store.commit({
					type: "addKanji",
					payload: char,
					//  {
					// 	kanji: char
					// }
				});
				// this.userLists.knownKanji.push(charFromHTML);
			}
		},
	},
	created() {
		this.searchKanjiWords();
	},
};
</script>

