<template>
	<div>
		<!-- this will be linked to a single word so no need to pass direcly into method, it will be passed into the component -->
		<button class="btn btn-outline-success" @click="addCharacter">
			Add Kanji to Known
		</button>
	</div>
</template>

<script>
import * as util from "../util.js";

import Vue from "vue";
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";

import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

export default {
	name: "IKnowThisKanji",
	props: ["kanji"],
	data: function () { return {}},
	methods: {
		addCharacter() {
			console.log("adding " + this.kanji);
				this.$store.commit({
					type: "addKanji",
					payload: this.kanji
				});
		},
	},
	created() {
	},
};
</script>

