function getKanji(input) {
    //use regex to remove all non-kanji characters from the input
    //regex check for kanji by unicode: https://stackoverflow.com/questions/15033196/using-javascript-to-check-whether-a-string-contains-japanese-characters-includi/15034560
    //unicode values for all Japanese characters: http://www.rikai.com/library/kanjitables/kanji_codes.unicode.shtml
    const regexAllKanji = /[\u4e00-\u9faf\u3400-\u4dbf]/g; //this includes the Common, Uncommon, and Rare from the above links
    let kanji = input.match(regexAllKanji);
    return kanji;
}

function isKanji(input) {
    // console.log(input);
    // console.log("pooopeee");
    const regexAllKanji = /[\u4e00-\u9faf\u3400-\u4dbf]/g;
    let kanji = input.match(regexAllKanji);
    let isKan = kanji != null;
    // console.log(isKan);
    return isKan;
}

