function fetchApiData(apiURL, apiRequestData) {
    // console.log("searching " + apiRequestData);
    let responseArray = [];
    console.log(apiURL + apiRequestData);
    fetch(apiURL + apiRequestData)
        .then((response) => {
            if (!response.ok) {
                throw Error(`ERROR: ${response.statusText}`);
            }
            return response.json();
        })
        .then((json) => {
            // console.log(json);
            for (const obj of json) {
                responseArray.push(obj);
            }
        });
    
    return responseArray;
}

export { getKanji, isKanji, fetchApiData };